import {
  mapState,
  mapMutations
} from 'vuex'
import {
  queryLogin
} from '../../api'
export default {
  computed: {
    ...mapState(['user'])
  },
  data() {
    return {
      info: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    ...mapMutations({
      login: 'LOGIN',
      setWeek: 'SET_WEEK',
      updateUser: 'UPDATE_USER'
    }),
    toLogin() {
      if (this.checkFormat()) { //不为空则进行登录请求
        queryLogin(this.info).then(res => {
          if (res[1].data.infoCode === "200") {
            uni.showToast({
              icon: 'none',
              title: '登录成功'
            })
            setTimeout(() => {
              //设置用户
              this.setUser(res[1].data);
              //vuex记录user,week数据,设置登录状态hasLogin为True
              this.login(this.user);
              this.setWeek(res[1].data.currWeek);
              this.toPage(this.user);
            }, 2000);
          } else { //为空则提示报错信息
            uni.showToast({
              icon: 'none',
              title: res[1].data.infoText
            })
          }
        })
      }
    },
    setUser(data) {
      let params = {
        userId: data.userId,
        groupId: data.groupId,
        groupName: data.groupName,
        status: data.status,
        userName: data.userName,
      };
      this.updateUser(params);
    },
    toPage(user) {
      if (user.groupId !== 0) {
        uni.reLaunch({
          url: '/pages/main/main'
        })
      } else {
        uni.reLaunch({
          url: '/pages/group/group'
        })
      }
    },
    checkFormat() {
      if (this.info.email === "") {
        uni.showToast({
          icon: 'none',
          title: '请填写邮箱'
        });
        return false;
      } else if (!this.info.password) {
        uni.showToast({
          icon: 'none',
          title: '请填写密码'
        });
        return false;
      }
      return true;
    }
  }
}
